@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-regular"),
    url("../fonts/roboto-v29-latin_cyrillic-ext-regular.woff2") format("woff2"),
    url("../fonts/roboto-v29-latin_cyrillic-ext-regular.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-bold"),
    url("../fonts/roboto-v29-latin_cyrillic-ext-700.woff2") format("woff2"),
    url("../fonts/roboto-v29-latin_cyrillic-ext-700.woff") format("woff");
}
