.job {
  background-image: url("../img/fon_kak_rabotaem.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  background-color: #0b7784;
}

.job__container {
  padding-top: 150px;
  padding-bottom: 160px;
  padding-left: 20px;
  padding-right: 20px;

  @media (max-width: $viewport--md) {
    padding-top: 70px;
    padding-bottom: 80px;
  }
}

.job__container h2 {
  font-weight: 700;
  font-size: 54px;
  line-height: 56px;
  text-align: center;
  color: $white;
  margin: 0;
  margin-bottom: 100px;

  @media (max-width: $viewport--xl) {
    font-size: 48px;
    line-height: 50px;
  }

  @media (max-width: $viewport--lg) {
    font-size: 42px;
    line-height: 44px;
    margin-bottom: 100px;
  }

  @media (max-width: $viewport--md) {
    font-size: 38px;
    line-height: 40px;
    margin-bottom: 50px;
  }

  @media (max-width: $viewport--sx) {
    font-size: 32px;
    line-height: 34px;
  }
}

.job__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1080px;
  margin: 0 auto;
  align-items: baseline;

  @media (max-width: 1080px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.job__item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 315px;
  margin-bottom: 50px;

  @media (max-width: $viewport--md) {
    width: 100%;
  }
}

.job__number {
  width: 135px;
  height: 135px;
  background-color: #e8cc45;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.job__number span {
  font-size: 85px;
  line-height: 87px;
}

.job__item p {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  margin-top: 45px;
  color: $white;

  @media (max-width: $viewport--sx) {
    font-size: 16px;
    line-height: 18px;
  }
}

.job__button {
  margin: 0 auto;
  display: block;
}
